export default{
	data(){
		return{
			//登录判断，判断是否是短信登录
			msgLogin:'isMsgLogin',
			phone:'',					//电话，账号
			yzm:'',						//验证码
			password:'',				//密码
			second:''					//验证码需要的秒
		}
	},
	methods:{
		//短信登录、账号登录点击
		orderLogin(id){
			if(id=='isMsgLogin'){
				this.msgLogin='isAccountLogin'
			}else{
				this.msgLogin='isMsgLogin'
			}
		},
		//获取验证码点击
		yzmBtn(){
			console.log(1);
			let _self=this
			let s=60
			let stime=setInterval(function(){
				s--;
				if(s==0){
					_self.second=''
					clearInterval(stime)
				}else{
					_self.second=s
				}
			},1000)
		}
		
	},
	mounted() {
		
	}
}
